                    xAxis.push(res[i].price);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].price
                    })
                }
                var option = {};
				let titleObj = this.pie3?this.pie3.title:this.pie.title
